.banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.25) 100%), url("../../images/banner.jpg");
  background-size: cover;
  aspect-ratio: 1.8448 / 1;
  padding: 16px 24px;
}

.banner__text {
  width: 66%;
  max-width: 1000px;
  color: var(--text-color-light, #fff);
  font-size: 22px;
  text-align: center;
}

.banner h1 {
  line-height: 1.2;
  white-space: pre-line;
  word-wrap: break-word;
  margin: 0 0 8px 0;
}

.banner p {
  margin: 8px 0;
}

